
*{
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}
body{
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.app{
  background-image: url('./img/bgc1.jpg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4 ease-out;
  background-blend-mode:lighten;
}
.rev{
  flex-direction: row-reverse;
}
.app.warm{
  background-image: url('./img/bgs2.jpg');
}
main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2),rgba(0,0,0,0.75));
  padding: 25px;
}
.search-box{
  width: 100%;
  margin: 0 0 75px;
}
.btn{
  display: block;
  margin: auto;
  background-color: transparent;
  border: 3px solid white;
  border-radius: 14px;
  font-size: 16px;
  font-weight: 700;
}
.btn:hover{
  background-color: grey;
}
 .search-box .search-bar{
  display: block;
  width: 32%;
  padding: 15px;
  margin: auto;
  margin-bottom: 21px;

  appearance: none;
  background: none;
  border: none;
  outline: none;


  background-color: rgba(255,255,255,0.5);
  border-radius: 0 0 16px 16px;
  /* margin-top: -25px; */

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #313131;
  font-size: 20px;

  transition: 00.4s ease;
}
.search-box .search-bar:focus{
  background-color: rgba(255,255,255,0.75);
} 

.location-box .location{
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50,30,70,0.5);
}
.location-box .date{
  color: #fff;
  font-size: 20px;
  font-weight: 300px;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50,50,70,0.5);
}
.weather-box{
  text-align: center;
}
.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255,255,255,0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 900px;

  text-shadow: 3px 6px rgba(50,30,70,0.5);
  text-align: center;
  text-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather{
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50,50,70,0.5);
}

@media only screen and (max-Width:1140px){
  .search-box .search-bar{
    margin-top: -25px;
    width: 100%;
  }
}